@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&family=Spartan:wght@700&display=swap);
*,*::before,*::after{font-family:'Josefin Sans', sans-serif;box-sizing:border-box;margin:0;padding:0;border:0}:root{--primary-bg-color: #fafafa;--secondary-bg-color: #fff;--bg-image: url(/react-to-do/static/media/bg-desktop-light.3508d620.jpg) no-repeat;--box-shadow: 0px 35px 50px -15px rgba(194, 195, 214, 0.5);--circle-border-color: #e3e4f1;--circle-border-hover-and-bg-color: linear-gradient(
    135deg,
    #55ddff 0%,
    #c058f3 100%
  );--input-placeholder-text-color: #9394a5;--input-text-color: #393a4b;--todo-text-color: #494c6b;--status-bar-btn-color: #9495a5;--status-bar-btn-hover-color: #494c6b;--status-bar-btn-active-color: #3a7cfd}.darkTheme{--primary-bg-color: #171823;--secondary-bg-color: #25273d;--box-shadow: 0px 35px 50px -15px rgba(0, 0, 0, 0.5);--circle-border-color: #393a4c;--input-placeholder-text-color: #767992;--input-text-color: #c8cbe7;--todo-text-color: #c8cbe7;--status-bar-btn-color: #5b5e7e;--status-bar-btn-hover-color: #e3e4f1}body,#root{width:100vw;height:100vh}body{background:#fafafa;background:var(--primary-bg-color)}#root{background:url(/react-to-do/static/media/bg-desktop-light.3508d620.jpg) no-repeat;background:var(--bg-image);background-size:contain}main{max-width:540px;margin:auto;padding-top:70px}

header{display:flex;justify-content:space-between;align-items:flex-end;margin-bottom:48px}header h1{color:#fff;font-size:40px;line-height:40px;letter-spacing:15px}header .sun{transition:0.3s ease-in-out}header .sun:hover{-webkit-transform:scale(1.1) rotate(-120deg);transform:scale(1.1) rotate(-120deg)}header .sun:hover path{fill:#f6ed00}header .moon{transition:0.3s ease-in-out}header .moon:hover{-webkit-transform:scale(1.1) rotate(-120deg);transform:scale(1.1) rotate(-120deg)}header .moon:hover path{fill:#2d2d2d}

form{height:64px;border-radius:5px;overflow:hidden;box-shadow:var(--box-shadow);margin-bottom:24px;position:relative}form .circle-box{position:absolute;height:24px;width:24px;border:1px solid var(--circle-border-color);border-radius:50%;top:20px;left:24px}form input{height:100%;width:100%;border:none;font-size:18px;line-height:18px;letter-spacing:-0.25px;color:var(--input-text-color);padding:23px 72px;outline:none;caret-color:#3a7cfd;background:var(--secondary-bg-color)}form input::-webkit-input-placeholder{color:var(--input-placeholder-text-color)}form input:-ms-input-placeholder{color:var(--input-placeholder-text-color)}form input::placeholder{color:var(--input-placeholder-text-color)}

.completed{background:var(--circle-border-hover-and-bg-color) !important;border:none !important}.showSVG{visibility:visible !important}.todo__complete-btn{display:grid;place-items:center;position:absolute;top:20px;left:24px;border:1px solid var(--circle-border-color);border-radius:50%;height:24px;width:24px;cursor:pointer;transition:0.3s}.todo__complete-btn:hover{border:none;background:var(--circle-border-hover-and-bg-color)}.todo__complete-btn .btn-bg-filler{display:grid;place-items:center;height:22px;width:22px;border-radius:50%;background:var(--secondary-bg-color);transition:0.3s}.todo__complete-btn .btn-bg-filler svg{visibility:hidden}

.todo__delete-btn{position:absolute;top:22.72px;right:24px;transition:0.3s;-webkit-transform:scale(0);transform:scale(0);cursor:pointer}

.todo__content{color:var(--todo-text-color);font-size:18px;line-height:18px;letter-spacing:-0.25px}.cross-out{color:#d1d2da;text-decoration:line-through}

.todo{position:relative;height:63px;font-size:18px;padding:21px 72px 25px 72px;border-bottom:1px solid var(--circle-border-color);-webkit-transform:0.3s;transform:0.3s}.todo:hover .todo__delete-btn{-webkit-transform:scale(1);transform:scale(1)}

.todos{list-style:none;background:var(--secondary-bg-color);box-shadow:var(--box-shadow);border-radius:5px 5px 0 0;max-height:439px;overflow:auto}

.status{display:flex;justify-content:space-between;align-items:center;height:50px;padding:0 24px;font-size:14px;line-height:14px;letter-spacing:-0.194444px;color:var(--status-bar-btn-color);background:var(--secondary-bg-color);border-radius:0 0 5px 5px}.status__all,.status__active,.status__completed{font-weight:700}.status__all{margin-right:19px}.status__active{margin-right:18px}.status-btns{cursor:pointer;color:var(--status-bar-btn-color);background:var(--secondary-bg-color)}.status-btns:hover{color:var(--status-bar-btn-hover-color)}.active{color:var(--status-bar-btn-active-color) !important}

